<template>
  <div class="body-bg">
    <headerBar :header-data="headerData" />
    <div class="container-ca">
      <div class="login-cainfo">
        <div class="ca-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.img1" /></div>
        <div class="ca-text">CA移动证书</div>
      </div>
      <div class="login-form">
        <van-cell-group>
          <div class="input-item">
            <van-field
              ref="name"
              v-model="user.userAccount"
              v-validate="'required|mmLength'"
              clearable
              is-highlight
              is-title-latent
              placeholder="请输入移动证书Pin码"
              name="PIN"
              :error-message="errors.first('PIN')"
            />
          </div>

        </van-cell-group>
        <van-notice-bar
          style="margin-top:15px;"
          left-icon="info-o"
          text="此密码将作为您以后的移动证书登录密码,请妥善保管！"
        />
        <div class="login-button">
          <van-button round type="info" @click="goScan()">扫码加密</van-button>
        </div>

      </div>
    </div>

    <!-- 证书安装成功弹窗 begin -->
    <van-popup v-model="show" closeable round style="width:90%;height:320px;">
      <div class="information-tips">
        <div class="tips-icon"><van-image fit="cover" width="100%" height="100%" :src="imageList.tipsimg1" /></div>
        <div class="information-tips-tit">CA移动证书下载成功</div>
        <div class="information-tips-txt">CA移动证书下载成功</div>

        <div class="information-tips-btn">
          <ul>
            <li><van-button plain type="info" size="large" round>返回首页</van-button></li>
            <li><van-button type="info" size="large" round>立即登录</van-button></li>
          </ul>
        </div>
      </div>
    </van-popup>

    <!-- 证书安装成功弹窗 end -->
  </div>

</template>

<script>
import headerBar from '@/components/header/headerBar.vue'
import { pageRouter, NativeCaJS, NativeJS } from '@/common/js/mixin.js'
import { loadSearch } from '@/common/js/catch'
import { caSmJJm, getSfxx } from '@/store/module.js'
export default {
  name: 'CaCz',
  components: {
    headerBar
  },

  mixins: [pageRouter, NativeCaJS, NativeJS],
  data() {
    return {
      Native_JJmMessage: '',
      headerData: {
        title: '扫码加密',
        leftUrl: 'back',
        leftText: '首页'
      },
      user: { userAccount: '' },
      PIN: '', // 密码
      imageList: {
        img1: require('@/assets/images/login/icon-jiami.png'),
        tipsimg1: require('@/assets/images/icon-success.png'), /* 成功 */
        tipsimg2: require('@/assets/images/icon-info.png'), /* 警告 */
        tipsimg3: require('@/assets/images/icon-error.png') /* 失败 */
      },
      titleImage: true, // 标题图片
      eyesChange: true, // 是否展示密码
      show: false,
      token: '' // token
    }
  },
  watch: {
  // 二维码数据
    Native_ScanData(val) {
      console.info('val===0==', val.name)
      if (val) {
        console.info('val===1==', val.name)
        // alert(val.name)

        this.getScanData(val.name)
      }
    },
    // ca签名结果
    Native_JJmMessage(val) {
      console.info('Native_JJmMessage===0==', val)
      if (val) {
        console.info('Native_JJmMessage===1==', val)
        this.goCaSmJJm(val)
      }
    }
  },
  methods: {
    // 控制是否展示密码
    showEye() {
      this.eyesChange = !this.eyesChange
    },
    // 下载
    goScan() {
      this.$validator.validate().then(result => {
        console.info('result=====', result)
        if (result) {
          this.NativeScan()
        }
      })
    },
    async getScanData(token) {
      this.token = token
      getSfxx.token = token
      this.asyncApiModule(getSfxx).then(res => {
        res = JSON.parse(res)
        this.zjmessage = res
        if (res !== null) {
          console.log('res=====', res)
          // this.NativeSignMessage(res)
          const ca = loadSearch('caListItem')[0]
          const ca1 = JSON.parse(ca)
          const pins = loadSearch('caListPin')[0]
          // const str = ca1.dn
          // var str = element.subjectDN
          // if (this.columns.indexOf(str.split('@')[1]) === -1) {
          const pin = { 'CAXLH': ca1.serialNo,
            'ca': ca1,
            'newPin': pins,
            'kzzd': res,
            'name': '测试供应商'
          }
          console.log('pin==11===', pin)
          if (process.env.NODE_ENV !== 'development') {
            try {
              this.Native_JJmMessage = window.myapp.envelopeEncrypt(JSON.stringify(pin))
            } catch (err) {
              console.log(err)
            }
            this.$bridge.callhandler('signMessage', res, (data) => {
              const datas = JSON.parse(data)
              console.log('goScanView------', data)
              this.Native_SignMessage = datas
            })
          } else {
            this.$toast('返回原生页面')
          }
        }
        // this.needIdCard = true
        // this.$toast.success('扫描成功')
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },
    // 下载
    goSing(data) {

    },
    // 签名接口
    goCaSmJJm(vaule) {
      const data = { 'value': vaule, 'token': this.token }
      console.log('zjmessage=====', data)
      caSmJJm.data = data
      this.asyncApiModule(caSmJJm).then(res => {
        res = JSON.parse(res)
        // this.zjmessage = res
        console.log('zjmessage=====', res)
        // if (res !== null) {

        // }
      }).catch(error => {
        this.$toast(error)
        console.log('error=====', error)
      })
    },

    showPopup() {
      this.show = true
    }

  }

}

</script>

<style lang="css" scoped>
.nav-bar{box-shadow: none;}
</style>
